.movie__container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.movie {
  width: 310px;
  height: 460px;
  margin: 1rem;
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  border: none;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.movie__year {
  position: absolute;
  padding: 16px;
  width: 100%;
  opacity: 0;
  top: 0;
  color: #f9d3b4;
}

.movie:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.movie__poster {
  width: 100%;
  height: 100%;
}

.movie__img {
  height: 100%;
  width: 100%;
}

.movie__info {
  z-index: 2;
  background-color: #343739;
  padding: 16px 24px 24px 24px;

  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}

.movie__type {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #f0f0f0;
}

.movie__title {
  font-family: "Roboto Slab", serif;
  color: #f9d3b4;
  text-align: center;
  margin: 5px auto 0;
}

.movie:hover .movie__poster {
  height: 100%;
  opacity: 0.3;
}

.movie:hover .movie__info {
  background-color: transparent;
}

.movie:hover .movie__year {
  opacity: 1.0;
}

@media screen and (max-width: 600px) {
  .app {
    padding: 2rem 2rem;
  }

  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .search__input {
    font-size: 1rem;
  }

  .search__img {
    width: 20px;
    height: 20px;
  }
}

@media screen and (max-width: 400px) {
  .app {
    padding: 4rem 1rem;
  }

  h1 {
    font-size: 2rem;
  }

  .movie__container {
    margin-top: 0.5rem;
  }

  .movie {
    width: "100%";
    margin: 1rem;
  }
}

//ADDITIONAL STYLES
.loading,
.error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  color:  rgba(249, 211, 180, 1);
  height: 30vh;
}

.error__p {
  font-weight: bold;
  margin-bottom: 1.5rem;
}

.loading-indicator {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: lightblue;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% {
    opacity: 0.4;
    transform: scale(0.8);
  }

  50% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: 0.4;
    transform: scale(0.8);
  }
}