/* || ##GENERAL STYLES */
html {
  scroll-behavior: smooth;
  text-align: center;
}

section {
  margin-bottom: 4em;
  scroll-margin-top: 16vh;
}

article {
  margin: 2em 0;
}

/* || ##UTILITY CLASSES */
/* #TEXT */
.text-highlight--primary {
  color: rgb(35, 187, 207);
}

.text-highlight--secondary {
  color: teal;
  font-style: italic;
}

.text-highlight--tertiary {
  color: brown;
  font-style: italic;
}

.text-shadow--tertiary {
  font-size: larger;
  color: teal;
  text-shadow: 3px 3px 3px brown;
}

.text-center {
  text-align: center;
}

.text-justify {
  text-align: justify;
}

.no-wrap {
  white-space: nowrap;
}

.first-character {
  float: left;
  font-family: Georgia, serif;
  font-size: 320%;
  line-height: 90%;
  padding-top: 1px;
  padding-right: 1px;
  padding-left: 1px;
  color: rgb(35, 187, 207);
}

