/* #LINKS */
.link {
  text-decoration: none;
  color: unset;
  font-size: medium;
  font-family: "Familjen Grotesk", sans-serif;
  padding: 0px;
}

.link:hover {
  color: teal;
  font-weight: bolder;
}

.link-highlight {
  color: rgb(35, 187, 207);
}

/* #ICONS */
.icon-highlight--primary {
  color: rgb(35, 187, 207);
}

.icon-highlight--primary:hover {
  color: teal;
  font-weight: bolder;
}

.icon-highlight--tertiary {
  color: brown;
}

.box--center {
  display: block;
  margin: auto;
}

/* #MARGINS */
.margin--sm {
  margin: 0.7em auto 2em;
}

* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

:root {
  --font-roboto: "Roboto Slab", serif;
  --font-raleway: "Raleway", sans-serif;
}

body {
  font-family: var(--font-roboto);
  background-color: #212426;
}

.movie-id {
  color: #a1a1a1;
}

.app {
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

h1 {
  font-size: 3rem;
  letter-spacing: 0.9px;
  background: linear-gradient(90deg, rgb(249, 211, 180) 0%, rgba(249, 211, 180, 0.3) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: fit-content;
}

h3 {
  letter-spacing: 0.9px;
  background: linear-gradient(90deg, rgba(249, 211, 180, 0.3) 0%, rgb(249, 211, 180) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: fit-content;
}

.search {
  width: 71%;
  margin: 0.5rem 0 0.1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  background: #1f2123;
  -webkit-box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
  box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
}

.search__input {
  flex: 1;
  border: none;
  font-size: 1.5rem;
  font-family: var(--font-raleway);
  font-weight: 500;
  flex-grow: 1;
  padding-right: 0.3rem;
  outline: none;
  color: #a1a1a1;
  background: #1f2123;
}

.search__img {
  width: 35px;
  height: 35px;
  cursor: pointer;
}

